<template>
  <div class="pay_alter_box">
    <el-dialog :title="alertData.title" :visible.sync="DialogVisible" width="680px" center :append-to-body='true' :before-close="toCancleConfirm1" class="record_alert record_alert1">
      <div class="order_details">

        <!-- 显示信息 -->
        <div class="order_details_title">
          <span>订单详情</span>
          <span class="oreder_time" v-if="alertData.type!=3&&alertData.type!=4"  >{{alertData.fileInfo[0].date}}</span>
        </div>


        <!-- 各个类型样式 （舆情，征信，账号，智能文书）-->
        <div class="order_details_box">
          <!-- 付费账号 -->
          <div class="order_details_list_box" v-if="alertData.type==1">
            <div class="order_details_list" v-for="(item,index) of alertData.fileInfo " :key="index">
              <img src="../../images/icon_grzh.png" alt="" class="list_icon">
              <div class="list_info">
                <div class="list_file_name list_line_type">
                  <span>付费账号</span>
                  <span>￥{{alertData.onPrice}}</span>
                </div>
                <div class="list_file_count list_line_type">
                  <span>数量</span>
                  <span>X{{alertData.fileInfo.length}}</span>
                </div>
              </div>
            </div>
          </div>
          <!-- 智能文书 -->
          <div class="order_details_list_box" v-if="alertData.type==2">
            <div class="order_details_list" v-for="(item,index) of alertData.fileInfo " :key="index">
              <img src="../../images/icon_znws.png" alt="" class="list_icon">
              <div class="list_info">
                <div class="list_file_name list_line_type">
                  <span>{{item.pdf_name}}</span>
                  <span>￥{{item.price}}</span>
                </div>
                <div class="list_file_count list_line_type">
                  <span>数量</span>
                  <span>X{{alertData.fileInfo.length}}</span>
                </div>
              </div>
            </div>
          </div>
          <!-- 征信套餐 -->
          <div class="order_details_list_box order_details_list_box4" v-if="alertData.type==3">
            <div class="price_box" @click="calculationAmount(item.money)" v-for="(item,index) of tcData" :key="index" :class="{'price_box_hover':paymentAmount==item.money}">
              <div class="price_line_1">{{item.number}}个</div>
              <div class="price_line_2">￥{{item.money}}</div>
            </div>
          </div>
          <!-- 舆情监控 -->
          <div class="order_details_list_box order_details_list_box3" v-if="alertData.type==4">
            <div class="yq_price_box">
              <span>{{alertData.onPrice}}元/个/月</span>
            </div>
            <div class="yq_number_box">
              <div class="yq_number_title">购买数量</div>
              <div class="yq_number_btn">
                <button class="btn_mimus cursor" @click="toAddOrMinus(-1)" v-if="alertData.shopNumber>1">-</button>
                <button class="btn_mimus " v-else>-</button>
                <el-input v-model="alertData.shopNumber" class="header-right-time" @change="getData(1)"></el-input>
                <button class="btn_add cursor" @click="toAddOrMinus(1)">+</button>
              </div>
            </div>
          </div>
        </div>



        <!-- 支付金额 -->
        <div class="pay_number_box">
          <span>支付金额</span>
          <span class="pay_number">￥{{paymentAmount}}</span>
          <span class="pay_ts">（支付后可开发票）</span>
        </div>


        <!-- 支付方式选择 -->
        <div class="select_pay_way_box">
          <div class="pay_way_list " v-for="(item,index) of payWayData" :key="index" :class="{'pay_way_list_close':!item.open}">
            <div class="pay_way_list_left">
              <img :src="'../static/images/'+item.imgName+'.png'" alt="">
              <div class="pay_way_name_box">
                <span>{{item.name}}</span>
                <span class="pay_way_balance" v-if="item.balance!=''">账户余额：{{item.balance}}</span>
              </div>
            </div>
            <div class="pay_way_list_right">
              <span class="iconfont icon-xuanzhong" v-if="selectWay==item.type" @click="toSelect(item)"></span>
              <span class="iconfont icon-danxuanweixuanzhong" v-else @click="toSelect(item)"></span>
            </div>
          </div>
        </div>


        <!-- 支付按钮 -->
        <div class="pay_btn_box">
          <button @click="toPay">去支付</button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {
    getwalletInfo,
    WalletPay,
    toAlertZfb
  } from '../../config/api';
  import {
    dialogConfirm
  } from '../../config/mUtils';
  export default {
    data() {
      return {
        //1、智能文书2、付费账号3、舆情4、征信,
        DialogVisible: true,
        payWayData: [
        	  {
            type: 3,
            name: "支付宝支付（推荐使用）",
            balance: "",
            open: true,
            imgName: "icon_zfbzf"
          },
          {
            type: 2,
            name: "企业账户支付",
            balance: 0,
            open: false,
            imgName: "icon_qyzf"
          },
          {
            type: 4,
            name: "微信支付",
            balance: "",
            open: false,
            imgName: "icon_wxzf"
          },
          {
            type: 1,
            name: "个人账户支付",
            balance: 0,
            open: false,
            imgName: "icon_grzf"
          }
        ],
  
        tcData: [{
          number: 100,
          money: 999
        }, {
          number: 1000,
          money: 8800
        }, {
          number: 7000,
          money: 48000
        }, {
          number: 15000,
          money: 98000
        }, ],
        selectWay: "",
        onePrice: "",
        paymentAmount: 0
        // keyWord: 1
      };
    },
    created() {
      if (this.alertData.type == 2) {
        this.alertData.fileInfo.forEach(element => {
          this.paymentAmount = this.paymentAmount + parseFloat(element.price);
        });
      }
      // console.log(this.alertData);
      this.getAccountInfo();
    },
    computed: {},
    props: {
      alertData: {
        type: Object,
        required: true
      }
    },
    methods: {
      toCancleConfirm1() {
        this.$emit("ee", "");
      },
      toSelect(item) {
        if (item.open) {
          this.selectWay = item.type;
        }
      },
      toPay() {
        let params = {
          order_type: this.alertData.type,
          order_count: 1,
        }
        // 支付方式
        if (this.selectWay == '') {
          dialogConfirm('info', '请选择支付方式')
          return false;
        } else {
          params.pay_type = this.selectWay;
        }
        //支付金额
        if (this.paymentAmount == 0) {
          dialogConfirm('info', '请选择支付金额')
          return false;
        } else {
          params.fee = this.paymentAmount;
        }
        //账号id 或者 智能文书的id
        if (this.alertData.type == 2 || this.alertData.type == 1) {
          if (this.alertData.fileInfo.length > 0) {
            let idStr = [];
            this.alertData.fileInfo.forEach(element => {
              idStr.push(element.id);
            });
            params.type_id = idStr.join(',');
          } else {
            if (this.alertData.type == 2) {
              dialogConfirm('info', '请选择要购买的文书')
            } else {
              dialogConfirm('info', '请选择要付费的账号')
            }
            return false;
          }
        }
  
        
        WalletPay(params).then(res => {
          
          if (res.data.msgNo == 1) {
            let data = res.data.payload;

            
            if(this.selectWay==1){
              // console.log(res);
              dialogConfirm('success', '购买成功');
              this.$emit("ee", "");
              this.$emit("getArticleDetails",this.alertData.currentPage );
            }else if(this.selectWay==2){
              // console.log(res);
            }else if(this.selectWay==3){
              const newTab = window.open();
              const div = document.createElement('div');
              div.innerHTML = data;
              newTab.document.body.appendChild(div);
              newTab.document.forms.alipaysubmit.submit();
              // let tempwindow = window.open("_blank"); // 先打开页面
              // tempwindow.location = res.data.payload; // 后更改页面地址
            }else{
              // console.log(res);
            }
            
          }else{
             dialogConfirm("info", res.data.message);
          }
  
        })
      },
  
      //加一和减一
      toAddOrMinus(value) {
        this.alertData.shopNumber = this.alertData.shopNumber + value;
        this.calculationAmount();
      },
  
      // 计算总金额
      calculationAmount(money) {
        if (this.alertData.type == 3) {
          this.paymentAmount = money;
        } else {
          this.paymentAmount = Math.round(this.alertData.onPrice * this.alertData.shopNumber * 100) / 100;
        }
      },
  
      //获取钱包信息
      getAccountInfo() {
        getwalletInfo().then(res => {
          // console.log(res);
          let data = res.data.payload;
          data.forEach((item, index) => {
            if (item.type == 1) {
              // console.log(item.balance);
              if (item.balance == 0) {
                item.balance = '0';
              }
              this.payWayData[3].balance = item.balance;
            }
            if (item.type == 2) {
              this.payWayData[1].balance = item.balance;
            }
          });
        })
      },
      
    }
  };
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  @import "../../style/apply";
  .order_details {}
  
  .order_details_title {
    display: flex;
    flex-direction: column;
    &>span {
      font-size: 14px;
      color: #282828;
    }
    .oreder_time {
      color: #808080;
      font-size: 10px;
    }
  }
  
  .order_details_box {}
  
  .order_details_list_box {}
  
  .order_details_list_box3 {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .order_details_list_box4 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .price_box {
      width: calc(25% - 15px);
      border: 1px solid #f1f1f1;
      border-radius: 6px;
      padding: 20px 32px;
      .price_line_1,
      .price_line_2 {
        // text-align: center;
        color: #f85959;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
      }
      .price_line_1 {
        font-size: 16px;
      }
      .price_line_2 {
        font-size: 24px;
      }
    }
    .price_box:hover {
      background-color: #f6fbff;
      border: 1px solid #479eff;
    }
    .price_box_hover {
      background-color: #f6fbff;
      border: 1px solid #479eff;
    }
  }
  
  .order_details_list {
    display: flex;
    align-items: center;
    &>img {
      width: 58px;
      height: 58px;
    }
    .list_info {
      display: flex;
      flex-direction: column;
      width: calc(100% - 58px);
      padding-left: 10px;
    }
    .list_line_type {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .list_file_name span {
      font-size: 14px;
      color: #282828;
    }
    .list_file_count span {
      font-size: 12px;
      color: #808080;
    }
  }
  
  .yq_price_box {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 190px;
    height: 67px;
    background-color: #f6fbff;
    border: 1px solid #479eff;
    border-radius: 6px;
    margin-right: 100px;
    &>span {
      color: #f85959;
      font-size: 18px;
    }
  }
  
  .yq_number_box {
    display: flex;
    flex-direction: column;
    .yq_number_title {
      font-size: 14px;
      margin-bottom: 5px;
      color: #282828;
      text-align: center;
    }
    .yq_number_btn {
      display: flex;
      align-items: center;
      .btn_mimus,
      .btn_add {
        width: 42px;
        height: 30px;
        font-size: 16px;
        color: #282828;
        border: 1px solid #f1f1f1;
      }
      .btn_mimus {
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
        border-right: none;
      }
      .btn_add {
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
        border-left: none;
      }
    }
  }
  
  .pay_number_box {
    background-color: #f9f8f6;
    padding: 3px 14px;
    margin-top: 14px;
    border-radius: 2px;
    span {
      font-size: 14px;
      color: #282828;
    }
    .pay_number {
      color: #ff7676;
      padding: 0px 5px;
      font-size: 16px;
    }
    .pay_ts {
      color: #808080;
    }
  }
  
  .select_pay_way_box {
    padding: 0px 15px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  
  .pay_way_list {
    width: calc(50% - 30px);
    height: 65px;
    padding: 8px 0px;
    border-bottom: 1px solid #f1f1f1;
    display: flex;
    //   align-items: center;
    justify-content: space-between;
    .pay_way_list_left {
      display: flex;
      align-items: center;
      img {
        width: 28px;
        height: 28px;
        margin-right: 18px;
      }
      .pay_way_name_box {
        display: flex;
        flex-direction: column;
        span {
          font-size: 14px;
          color: #282828;
        }
        .pay_way_balance {
          font-size: 10px;
        }
      }
    }
    .pay_way_list_right {
      line-height: 48px;
      .iconfont {
        font-size: 18px;
        color: #dbd7d7;
        border-radius: 50%;
        cursor: pointer;
      }
      .icon-xuanzhong {
        color: #1787fb;
      }
    }
  }
  
  .pay_way_list:nth-child(odd) {
    margin-right: 45px;
  }
  
  .pay_way_list_close {
    .pay_way_list_left {
      .pay_way_name_box {
        span {
          color: #a0a4aa;
        }
      }
    }
    .pay_way_list_right {
      .iconfont {
        background-color: #f3f3f3;
        cursor: default;
      }
    }
  }
  
  .pay_btn_box {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    button {
      width: 271px;
      height: 45px;
      background: linear-gradient( 125deg, rgba(113, 176, 255, 1) 0%, rgba(85, 158, 248, 1) 49%, rgba(56, 133, 228, 1) 100%);
      border-radius: 31px;
      color: white;
      font-size: 18px;
    }
  }
</style>



