<template>
  <div class="personal-content-body">
    <div class="content">
      <div class="content-title clear">
        <div class="left">智能文书</div>
        <el-input placeholder="搜索" v-model="keyWord" class="header-right-time right" :clearable='true' @change="getArticleDetails(1)">
          <i slot="prefix" class="el-input__icon el-icon-search  "></i>
        </el-input>
      </div>
      <!-- <div class="search">
        <el-input placeholder="搜索" v-model="keyWord" class="header-right-time" :clearable='true' @change="getArticleDetails(1)">
          <i slot="prefix" class="el-input__icon el-icon-search  "></i>
        </el-input>
      </div> -->
      <div class="list-contaienr">
        <div class="list-header">
          <span class="font-size: 12px;">{{totalTxt}}</span>
          <div class="list-header—button-box">
            <div class="ygws_btn" @click="toYgPage">
              <img src="../../images/icon_wd.png" alt="">
              <span>已购文书</span>
            </div>
          </div>
        </div>
        <!-- <div class="list-line"></div> -->
        <!-- <div class="list-nav">
          <span @click="goBack('tuneUpAssistantList')">尽调助手&nbsp;&nbsp;></span>
          <span>智能文书</span>
        </div> -->
        
        <div class="list-content-items">
          <div class="banner_box">
            <img src="../../images/jdzsHomepageZnws_text.png" alt="">
          </div>
          
          <div class="list-content-item" v-for="(item,index) of dataList" :key="index">
            <img src="../../images/icon_pdf.png" class="item_file_type" alt="">
            <div class="item_file_name">
              <div class="item_file_name_info" @click="toShowFilePage(item.pdf_name,item.url,item)"> {{item.pdf_name}}</div>
              <div class="money_info">
                  <div class="have_buy" v-if="item.status==1">
                    <img src="../../images/icon_flag.png" alt="">
                    <span>已购买</span>
                  </div>
                  <span class="no_buy" v-else @click="toAlert(item)">¥ {{item.price}} 去购买</span>
              </div>
            </div>
            <span class="iconfont icon-bianji " @click="toDetails(item)" ></span>
            <!--<span class="iconfont icon-bianji iconfont1" v-else></span>-->
            
            <a :href="item.url" v-on:click.prevent="download(item)"><span class="iconfont icon-daoru"></span></a>
            
            <span class="item_file_size">{{item.size==null||item.size==''?'未知':item.size}}</span>
            <!-- <span class="item_file_time">{{item.date==null||item.date==''?'未知':item.date}}</span> -->
          </div>
          
          <div class="table-nodata flex_center" v-if="total==0">
            <img src="../../images/table_nodata.png"> 
            <span>没有对应数据</span>
          </div>
          <div class="pagination-normal" v-if="total!=0">
            <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
    <pay-money-page v-bind:alertData='alertData' v-bind:alerStatus='alerStatus'  @ee="hideAlertBox"  @getArticleDetails="getArticleDetails" v-if="isShow"></pay-money-page>
  </div>
</template>

<script>
  import {
    industryReport
  } from "../../config/api";
  import {
    dialogConfirm,
    dialogConfirmHasbtn
  } from "../../config/mUtils";
  import payMoneyPage from './payMoneyPage';
  export default {
    data() {
      return {
        currentPage: 1,
        keyWord: "",
        pageSize: 10,
        total: '',
        totalTxt: '',
        dataList: [],
        valueObj:{},
        alertData:{
          title:'智能文书',
          type:2,//1、账号付费 2、智能文书3、征信 4、舆情,
          fileInfo:[],
          // onPrice:0.99,
          shopNumber:1,
        },
        alerStatus:false,
        isShow:false,
      };
    },
    mounted() {
      this.valueObj = this.$route.query;
      let page = 1;
      if (this.$route.query.page) {
        page = parseInt(this.$route.query.page);
      }
      this.getArticleDetails(page);
    },
    components:{payMoneyPage},
    methods: {
      //获取详情信息
      currentChange(val) {
        if (this.total > this.pageSize) {
          this.getArticleDetails(val);
        }
      },
      getArticleDetails(val) {
        // console.log(val);
        this.currentPage = val;
        industryReport({
          type: 3,
          page: this.currentPage,
          pageSize: this.pageSize,
          keywords: this.keyWord ,
          condition:1
          // category:this.valueObj.category
        }).then(res => {
          if (res.data.msgNo == 1) {
            if (res.data.payload != null && res.data.payload != '') {
              this.total = res.data.payload.count
              this.totalTxt = '共' + res.data.payload.count + '条结果';
  
              this.dataList = res.data.payload.data;
              // console.log(res);
            } else {
              this.total = 0
              this.totalTxt = '共0条结果';
              this.dataList = [];
            }
          } else {
            dialogConfirm("info", res.data.message);
          }
        });
      },
      goBack(name) {
        this.$router.push({
          name: name
        });
      },
      toShowFilePage(file_name, path2,item) {
        let url = path2;
        const {
          href
        } = this.$router.resolve({
          name: "documentView",
          query: {
            filetype: "pdf",
            url: url
          }
        });
        if (item.status == 1) {
        		window.open(href, "_blank");
        } else{
        		this.toAlert(item);
        }
      },
      todownLoad(href, name) {
        let a = document.createElement("a");
        a.href = href;
        a.download = name;
        a.click();
      },
      toYgPage(){
        this.$router.push({
          name: "jdzsHomepageZnwsYg"
        });
      },
      toAlert(item){
      	var date = new Date().Format('yyyy-MM-dd hh:mm:ss');
      	item.date = date;
        this.isShow = true;
        this.alertData.fileInfo = [];
        this.alertData.fileInfo.push(item);
        this.alertData.currentPage = this.currentPage;
      },
      toDetails(item){
        // console.log(item);
        if (item.status == 1) {
        		 this.$router.push({
		          name: "reportEditor",
		          query:{
		            from:1,//1是智能文书列表2、已购文书
		            id:item.id,
                name:item.pdf_name,
                page:this.currentPage
		          }
		     });
        } else{
        		this.toAlert(item);
        }
      },
      hideAlertBox(){
        this.isShow = false;
      },
      download(item){
      	if (item.status == 1) {
      		window.location.href = item.url;
      	} else{
      		this.toAlert(item);
      	}
      }
    }
  };
</script>

<style lang="scss" scoped>
  .content {
    width: 100%;
    min-height: 100%;
    background-color: white;
    padding: 20px 30px;
    // padding-top: 30px;
  }
  
  .hidden {
    display: none;
  }
  
  .content-title>div.left {
    font-size: 18px;
    line-height: 30px;
    color: #35383a;
    font-weight: bold;
  }
  
  .header-right-time {
    margin-right: 10px; // position: relative;
  }
  
  .list-contaienr {
    // border: 1px solid #e4e4e4;
    // padding: 20px 17px 50px 17px;
    position: relative;
  }
  .search{
    margin-bottom: 10px;
  }
  
  .list-header {
    margin-bottom: 13px;
    display: flex;
    align-items: center;
    position: relative;
    justify-content: space-between;
    &>span {
      text-align: left;
      color: #3c98e8;
      font-size: 14px;
      font-weight: bold;
      margin-right: 37px;
    }
    .list-header—button-box{
      // display: flex;
      // align-items: center;
      padding-right: 30px;
    }
    .ygws_btn{
      padding: 5px 10px 5px 8px;
      border: 1px solid #3C98E8;
      border-radius: 6px;
      display: flex;
      align-items: center;
      cursor: pointer;
      &>img{
        width: 20px;
        height: 20px;
        margin-right: 5px;
      }
      &>span{
        font-size: 14px;
        color: #3C98E8;
      }
      
    }
  }
  
  .list-nav {
    // padding-left: 14px;
    margin-bottom: 12px;
  }
  
  .list-nav span {
    font-size: 14px;
    color: #3c98e8;
    cursor: pointer;
  }
  
  .list-nav span:last-child {
    color: #35383a;
    cursor: inherit;
  }
  
  .list-line {
    border-bottom: 1px solid #f4f4f4;
    margin-bottom: 15px;
  }
  
  .file_down_btn {
    display: flex;
    align-items: center;
    width: 108px;
    height: 34px;
    border: 1px solid #3c98e8;
    border-radius: 6px;
    justify-content: center;
    position: absolute;
    right: 34px;
    cursor: pointer;
    &>span {
      color: #3c98e8;
    }
    &>span:nth-child(1) {
      font-size: 25px;
      margin-right: 5px;
    }
    &>span:nth-child(2) {
      font-size: 14px;
    }
  }
  
  .item_title {
    display: flex;
    justify-content: flex-end;
    padding-right: 13px;
    font-size: 14px;
    margin-top: 15px;
    margin-bottom: 6px;
    .item_title_name {
      width: calc(75% - 101px);
      color: #808080;
    }
    .item_title_icon {
      width: 51px;
      color: #808080;
    }
    .item_title_size {
      width: 10%;
      color: #808080;
    }
    .item_title_time {
      width: 15%;
      color: #808080;
    }
  }
  
  .list-content-item {
    display: flex;
    align-items: center;
    // padding-bottom: 30px;
    padding: 20px 0px;
    border-top: 1px solid #f1f1f1;
    .item_file_type {
      width: 30px;
      height: 30px;
      margin-right: 20px;
    }
    .item_file_name {
      display: flex;
      flex-direction: column;
      width: calc(75% - 110px);
      padding-right: 60px;
      // position: relative;
      // top: 15px;
      .item_file_name_info {
        color: #35383a;
        font-size: 16px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        // margin-bottom: 10px;
        cursor: pointer;
      }
      .item_file_name_info:hover {
        color: #3c98e8;
      }
      .item_file_name_type {
        // width: auto;
        font-size: 14px;
        padding: 2px 5px;
        color: #3c98e8;
        background-color: #e7f4ff;
      }
    }
    .iconfont {
      font-size: 20px;
      margin-right: 30px;
      color: #3c98e8;
      cursor: pointer;
    }
    
    .iconfont1{
      color: #eaeaea;
      cursor: Default;
    }
    .icon-bianji{
      position: relative;
      top: 2px;
    }
    .item_file_size,
    .item_file_time {
      font-size: 14px;
      color: #35383a;
    }
    .item_file_size {
      width: 10%;
    }
    .item_file_time {
      width: 15%;
    }
  }
  
  // .list-content-item:nth-child(odd) {
  //   background-color: #f6fbff;
  // }
  .list-content-item:hover {
    background-color: #F6FBFF
  }
  // .list-content-item:last-child{
  //   border-bottom: 1px solid #f1f1f1;
  // }
  .banner_box{
    display: flex;
    align-items: center;
    width: 100%;
    height: 53px;
    background: url("../../images/jdzsHomepageZnws_bg.png") center center no-repeat;
    background-size: cover;
    filter: progid:DXImageTransform.Microsoft.AlphaImageLoader(src='../../images/investevent-term-bg.png', sizingMethod='scale');
    padding-left: 25px;
    img{
      height: 23px;
    }
  }
  .money_info{
    display: flex;
    align-items: center;
    margin-top: 3px;
  }
  .no_buy{
    font-size: 14px;
    color: #FF7676;
    cursor: pointer;
  }
  .have_buy{
    display: flex;
    align-items: center;
    &>img{
      width: 9px;
      height: 12px;
      margin-right: 5px;
    }
    &>span{
      font-size: 14px;
      color: #FF7676;
    }
  }
  .table-nodata{
  	height: 300px;
  }
</style>



